.solve {
  text-align: center;
}

h2 {
  text-align: center;
}

.cen {
  text-align: center;
  margin-top: 20px;
}

p {
  margin-top: 10px;
}

body {
  background-color: #23719f;
}

.list {
  background-color: #e3e3e3;
  border-radius: 3px;
  margin: 5px 5px;
  padding: 10px;
  width: 300px;
}

.list > header {
  font-weight: bold;
  font-size: 15px;
  line-height: 18px;
  cursor: grab;
}

.list article {
  border-radius: 3px;
  margin-top: 5px;
}

.list .card {
  background-color: #fff;
  border-bottom: 1px solid #ccc;
  padding: 6px 8px;
  cursor: pointer;
  margin-top: 10px;
}

.list .card:hover {
  background-color: #f0f0f0;
}

.detail {
  font-size: 12px;
  color: #939393;
}

.hidden {
  display: none;
}

[data-fakenode] {
  background-color: gray;
}

.black_overlay {
  display: none;
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  background-color: black;
  z-index: 1001;
  -moz-opacity: 0.8;
  opacity: 0.8;
  filter: alpha(opacity=80);
}

.white_content {
  display: none;
  position: absolute;
  top: 25%;
  left: 25%;
  width: 50%;
  height: 50%;
  padding: 16px;
  border: 16px solid orange;
  background-color: white;
  z-index: 1002;
  overflow: auto;
}
